.section.masonry-gallery {
    padding: 100px 0;
    *zoom: 1;
    margin-left: -15px;
    margin-right: -15px;
}
.section.masonry-gallery:before {
    content: '';
    display: table;
}
.section.masonry-gallery:after {
    content: '';
    display: table;
    clear: both;
}
.section.masonry-gallery .masonry-items li {
    float: left;
    overflow: hidden;
    position: relative;
    font-size: 52px;
    margin-bottom: 15px;
    -webkit-box-flex: 0;
    -webkit-flex: 0 0 auto;
        -ms-flex: 0 0 auto;
            flex: 0 0 auto;
    width: calc(99.99% * 1/2 - 30px);
    margin-left: 15px;
    margin-right: 15px;
}
@media (min-width: $(desktop)) {
    .section.masonry-gallery .masonry-items li {
        -webkit-box-flex: 0;
        -webkit-flex: 0 0 auto;
            -ms-flex: 0 0 auto;
                flex: 0 0 auto;
        width: calc(99.99% * 1/4 - 30px);
        margin-left: 15px;
        margin-right: 15px;
    }
}
.section.masonry-gallery .masonry-items li:first-child,
            .section.masonry-gallery .masonry-items li:nth-child(5n + 6) {
    margin-bottom: 15px;
}
@media (min-width: $(desktop)) {
    .section.masonry-gallery .masonry-items li:first-child,
            .section.masonry-gallery .masonry-items li:nth-child(5n + 6) {
        -webkit-box-flex: 0;
        -webkit-flex: 0 0 auto;
            -ms-flex: 0 0 auto;
                flex: 0 0 auto;
        width: calc(99.99% * 1/2 - 30px);
        margin-left: 15px;
        margin-right: 15px;
    }
}
.section.masonry-gallery .masonry-items li:nth-child(5n + 6) {
    float: right;
}
.section.masonry-gallery .masonry-items li:nth-child(10n + 11) {
    float: left;
}
.section.masonry-gallery .masonry-items li.image:hover img {
    display: block;
    opacity: 0.5;
}
.section.masonry-gallery .masonry-items li.image:hover .icon {
    opacity: 1;
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}
.section.masonry-gallery .masonry-items li img {
    width: 100%;
    height: 100%;
    -o-object-position: 50% 50%;
       object-position: 50% 50%;
    -o-object-fit: cover;
       object-fit: cover;
    -webkit-transition: all 0.25s;
    transition: all 0.25s;
    display: block;
}
.section.masonry-gallery .masonry-items li .icon {
    position: absolute;
    top: 50%;
    left: 50%;
    color: $white;
    display: block;
    margin-top: -30px;
    margin-left: -20px;
    opacity: 0;
    -webkit-transition: all 0.25s;
    transition: all 0.25s;
}
